import {Injectable} from '@angular/core';
import {ToastController, LoadingController, Platform, Loading, AlertController} from 'ionic-angular';

import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';
import {AppVersion} from '@ionic-native/app-version';
import {Camera, CameraOptions} from '@ionic-native/camera';
import {Toast} from '@ionic-native/toast';
import {File, FileEntry} from '@ionic-native/file';
import {Transfer, TransferObject} from '@ionic-native/transfer';
import {InAppBrowser} from '@ionic-native/in-app-browser';
import {ImagePicker} from '@ionic-native/image-picker';
import {Network} from '@ionic-native/network';
import {AppMinimize} from "@ionic-native/app-minimize";

import {Position} from "../model/type";
import {
  APP_DOWNLOAD,
  APK_DOWNLOAD,
  IMAGE_SIZE,
  QUALITY_SIZE,
  REQUEST_TIMEOUT,
  APP_VERSION_SERVE_URL
} from "./Constants";
import {GlobalData} from "./GlobalData";
import {Observable} from "rxjs";
import {CommonService} from "../service/CommonService";
import {Http, Response} from "@angular/http";
import {Utils} from "./Utils";
import {Logger} from "./Logger";
declare var LocationPlugin;
declare var AMapNavigation;

@Injectable()
export class NativeService {
  private loading: Loading;
  private loadingIsOpen: boolean = false;

  constructor(private platform: Platform,
              private toastCtrl: ToastController,
              private alertCtrl: AlertController,
              private statusBar: StatusBar,
              private splashScreen: SplashScreen,
              private appVersion: AppVersion,
              private camera: Camera,
              private toast: Toast,
              private transfer: Transfer,
              private file: File,
              private inAppBrowser: InAppBrowser,
              private imagePicker: ImagePicker,
              private network: Network,
              private appMinimize: AppMinimize,
              private loadingCtrl: LoadingController,
              private globalData: GlobalData,
              public logger: Logger,
              private http: Http) {
  }


  /**
   * 
   */
  statusBarStyleDefault(): void {
    this.statusBar.styleDefault();
  }

  /**
   * 
   */
  splashScreenHide(): void {
    this.splashScreen.hide();
  }

  /**
   *  `unknown`, `ethernet`, `wifi`, `2g`, `3g`, `4g`, `cellular`, `none`
   */
  getNetworkType(): string {
    if (!this.isMobile()) {
      return 'wifi';
    }
    return this.network.type;
  }

  /**
   * 
   */
  isConnecting(): boolean {
    return this.getNetworkType() != 'none';
  }

  /**
   * app
   */
  minimize(): void {
    this.appMinimize.minimize()
  }

  /**
   * url
   */
  openUrlByBrowser(url: string): void {
    this.inAppBrowser.create(url, '_system');
  }

  /**
   * app
   */
  detectionUpgrade(): void {
    if (this.isMobile()) {
      //app
      this.getPackageName().subscribe(packageName => {
        let appName = packageName.substring(packageName.lastIndexOf('.') + 1);
        let appType = this.isAndroid() ? 'android' : 'ios';
        //app
        this.http.get(Utils.formatUrl(`${APP_VERSION_SERVE_URL}/app/${appName}/${appType}/latest/version`)).map((res: Response) => res.json()).subscribe(res => {
          //app
          this.getVersionNumber().subscribe(currentNo => {
            if (currentNo != res.version) {//
              if (res.isForcedUpdate == 1) {//
                this.alertCtrl.create({
                  title: '',
                  subTitle: '',
                  buttons: [{
                    text: '',
                    handler: () => {
                      this.downloadApp();
                    }
                  }
                  ]
                }).present();
              } else {
                this.alertCtrl.create({
                  title: '',
                  subTitle: ',',
                  buttons: [{text: ''},
                    {
                      text: '',
                      handler: () => {
                        this.downloadApp();
                      }
                    }
                  ]
                }).present();
              }
            }
          })
        });
      })
    }

  }

  /**
   * app
   */
  downloadApp(): void {
    if (this.isIos()) {//ios
      this.openUrlByBrowser(APP_DOWNLOAD);
    }
    if (this.isAndroid()) {//android
      //
      let alert = this.alertCtrl.create({
        title: '0%',
        enableBackdropDismiss: false,
        buttons: ['']
      });
      alert.present();

      const fileTransfer: TransferObject = this.transfer.create();
      const apk = this.file.externalRootDirectory + 'android.apk'; //apk

      //apk
      fileTransfer.download(APK_DOWNLOAD, apk).then(() => {
        window['install'].install(apk.replace('file://', ''));
      }, err => {
        alert.dismiss();
        this.logger.log(err, 'android app ');
        this.alertCtrl.create({
          title: '',
          subTitle: '',
          buttons: [
            {
              text: '',
              handler: () => {
                this.openUrlByBrowser(APP_DOWNLOAD);//
              }
            }
          ]
        }).present();
      });

      //
      fileTransfer.onProgress((event: ProgressEvent) => {
        let num = Math.floor(event.loaded / event.total * 100);
        if (num === 100) {
          alert.dismiss();
        } else {
          let title = document.getElementsByClassName('alert-title')[0];
          title && (title.innerHTML = '' + num + '%');
        }
      });
    }
  }

  /**
   * 
   */
  isMobile(): boolean {
    return this.platform.is('mobile') && !this.platform.is('mobileweb');
  }

  /**
   * android
   */
  isAndroid(): boolean {
    return this.isMobile() && this.platform.is('android');
  }

  /**
   * ios
   */
  isIos(): boolean {
    return this.isMobile() && (this.platform.is('ios') || this.platform.is('ipad') || this.platform.is('iphone'));
  }

  alert(title: string, subTitle: string = "",): void {
    this.alertCtrl.create({
      title: title,
      subTitle: subTitle,
      buttons: [{text: ''}]
    }).present();
  }

  /**
   * 
   * @param message 
   * @param duration 
   */
  showToast(message: string = '', duration: number = 2000): void {
    if (this.isMobile()) {
      this.toast.show(message, String(duration), 'center').subscribe();
    } else {
      this.toastCtrl.create({
        message: message,
        duration: duration,
        position: 'middle',
        showCloseButton: false
      }).present();
    }
  };

  /**
   * loading
   * @param content 
   */
  showLoading(content: string = ''): void {
    if (!this.globalData.showLoading) {
      return;
    }
    if (!this.loadingIsOpen) {
      this.loadingIsOpen = true;
      this.loading = this.loadingCtrl.create({
        content: content
      });
      this.loading.present();
      setTimeout(() => {
        this.loadingIsOpen && this.loading.dismiss();
        this.loadingIsOpen = false;
      }, REQUEST_TIMEOUT);
    }
  };

  /**
   * loading
   */
  hideLoading(): void {
    if (!this.globalData.showLoading) {
      this.globalData.showLoading = true;
    }
    this.loadingIsOpen && this.loading.dismiss();
    this.loadingIsOpen = false;
  };

  /**
   * cordova-plugin-camera
   * @param options
   */
  getPicture(options: CameraOptions = {}): Observable<string> {
    let ops: CameraOptions = Object.assign({
      sourceType: this.camera.PictureSourceType.CAMERA,//,CAMERA:,PHOTOLIBRARY:
      destinationType: this.camera.DestinationType.DATA_URL,//base64,DATA_URL:base64   FILE_URI:
      quality: QUALITY_SIZE,//0 - 100
      allowEdit: false,//
      encodingType: this.camera.EncodingType.JPEG,
      targetWidth: IMAGE_SIZE,//
      targetHeight: IMAGE_SIZE,//
      saveToPhotoAlbum: false,//
      correctOrientation: true//
    }, options);
    return Observable.create(observer => {
      this.camera.getPicture(ops).then((imgData: string) => {
        if (ops.destinationType === this.camera.DestinationType.DATA_URL) {
          observer.next('data:image/jpg;base64,' + imgData);
        } else {
          observer.next(imgData);
        }
      }).catch(err => {
        if (err == 20) {
          this.alert(',');
          return;
        }
        if (String(err).indexOf('cancel') != -1) {
          return;
        }
        this.logger.log(err, 'cordova-plugin-camera');
        this.alert('');
      });
    });
  };

  /**
   * 
   * @param options
   */
  getPictureByCamera(options: CameraOptions = {}): Observable<string> {
    let ops: CameraOptions = Object.assign({
      sourceType: this.camera.PictureSourceType.CAMERA,
      destinationType: this.camera.DestinationType.DATA_URL//DATA_URL: 0 base64, FILE_URI: 1
    }, options);
    return this.getPicture(ops);
  };

  /**
   * 
   * @param options
   */
  getPictureByPhotoLibrary(options: CameraOptions = {}): Observable<string> {
    let ops: CameraOptions = Object.assign({
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
      destinationType: this.camera.DestinationType.DATA_URL//DATA_URL: 0 base64, FILE_URI: 1
    }, options);
    return this.getPicture(ops);
  };

  /**
   * 
   * @param options
   */
  getMultiplePicture(options = {}): Observable<any> {
    let that = this;
    let ops = Object.assign({
      maximumImagesCount: 6,
      width: IMAGE_SIZE,//
      height: IMAGE_SIZE,//
      quality: QUALITY_SIZE//0 - 100
    }, options);
    return Observable.create(observer => {
      this.imagePicker.getPictures(ops).then(files => {
        let destinationType = options['destinationType'] || 0;//0:base64,1:url
        if (destinationType === 1) {
          observer.next(files);
        } else {
          let imgBase64s = [];//base64
          for (let fileUrl of files) {
            that.convertImgToBase64(fileUrl).subscribe(base64 => {
              imgBase64s.push(base64);
              if (imgBase64s.length === files.length) {
                observer.next(imgBase64s);
              }
            })
          }
        }
      }).catch(err => {
        this.logger.log(err, '');
        this.alert('');
      });
    });
  };

  /**
   * base64
   * @param path 
   */
  convertImgToBase64(path: string): Observable<string> {
    return Observable.create(observer => {
      this.file.resolveLocalFilesystemUrl(path).then((fileEnter: FileEntry) => {
        fileEnter.file(file => {
          let reader = new FileReader();
          reader.onloadend = function (e) {
            observer.next(this.result);
          };
          reader.readAsDataURL(file);
        });
      }).catch(err => {
        this.logger.log(err, 'base64');
      });
    });
  }

  /**
   * app,0.01
   * @description  /config.xmlversion
   */
  getVersionNumber(): Observable<string> {
    return Observable.create(observer => {
      this.appVersion.getVersionNumber().then((value: string) => {
        observer.next(value);
      }).catch(err => {
        this.logger.log(err, 'app');
      });
    });
  }

  /**
   * app name,
   * @description  /config.xmlname
   */
  getAppName(): Observable<string> {
    return Observable.create(observer => {
      this.appVersion.getAppName().then((value: string) => {
        observer.next(value);
      }).catch(err => {
        this.logger.log(err, 'app name');
      });
    });
  }

  /**
   * app/id,com.kit.ionic2tabs
   * @description  /config.xmlid
   */
  getPackageName(): Observable<string> {
    return Observable.create(observer => {
      this.appVersion.getPackageName().then((value: string) => {
        observer.next(value);
      }).catch(err => {
        this.logger.log(err, 'app');
      });
    });
  }

  /**
   * 
   */
  getUserLocation(): Observable<Position> {
    return Observable.create(observer => {
      if (this.isMobile()) {
        LocationPlugin.getLocation(data => {
          observer.next({'lng': data.longitude, 'lat': data.latitude});
        }, msg => {
          observer.error('');
          if (msg.indexOf('') == -1) {
            this.alert('');
            return;
          }
          this.alert('' + msg);
          this.logger.log(msg, '');
        });
      } else {
        console.log(',');
        observer.next({'lng': 113.350912, 'lat': 23.119495});
      }
    });
  }

  /**
   * 
   * @param startPoint 
   * @param endPoint 
   * @param type 0,1,
   */
  navigation(startPoint: Position, endPoint: Position, type = 1): Observable<string> {
    return Observable.create(observer => {
      if (this.platform.is('mobile') && !this.platform.is('mobileweb')) {
        AMapNavigation.navigation({
          lng: startPoint.lng,
          lat: startPoint.lat
        }, {
          lng: endPoint.lng,
          lat: endPoint.lat
        }, type, message => {
          observer.next(message);
        }, err => {
          this.logger.log(err, '');
          this.alert('');
        });
      } else {
        this.alert('');
      }
    });
  }

}



